import React, { Component } from "react";
import {
	Tabs,
	Tab,
	Grid,
	Card,
	CardTitle,
	CardActions,
	CardText,
	Button
} from "react-mdl";

class Projects extends Component {
	constructor(props) {
		super(props);
		this.state = { activeTab: 0 };
	}
	toggleCategories = () => {
		switch (this.state.activeTab) {
			case 0: // Case 0 == React
				return (
					<div className="projects-grid">
						{/* React project 1: portfolio website */}
						<Card
							shadow={5}
							style={{
								display: "inline-block",
								verticalAlign: "middle",
								width: "300px",
								margin: "1em"
							}}
						>
							<CardTitle
								style={{
									color: "black",
									height: "176px",
									background:
										"url(data:image/png;base64,) center / cover"
								}}
							>
								Portfolio Website
							</CardTitle>
							<CardText>
								A website made with ReactJS to show my
								repositories in a more organised manner.
							</CardText>
							<CardActions border>
								<Button
									colored
									href={
										"https://github.com/Taipan95/portfolio-website"
									}
								>
									GitHub
								</Button>
							</CardActions>
						</Card>

						{/* React project 2: OMDB Search*/}
						<Card
							shadow={5}
							style={{
								display: "inline-block",
								verticalAlign: "middle",
								width: "300px",
								margin: "1em"
							}}
						>
							<CardTitle
								style={{
									color: "black",
									height: "176px",
									background:
										"url(data:image/png;base64,) center / cover"
								}}
							>
								OMDB Search
							</CardTitle>
							<CardText>
								A project using OMDB to search for movies by
								title.
							</CardText>
							<CardActions border>
								<Button
									colored
									href={
										"https://github.com/Taipan95/react-omdb"
									}
								>
									GitHub
								</Button>
							</CardActions>
						</Card>
					</div>
				);
			case 1: // Case 1 == JavaScript
				return (
					<div className="projects-grid">
						<Card
							shadow={5}
							style={{
								display: "inline-block",
								verticalAlign: "middle",
								width: "300px",
								margin: "1em"
							}}
						>
							<CardTitle
								style={{
									color: "black",
									height: "176px",
									background:
										"url(https://a.thumbs.redditmedia.com/zDOFJTXd6fmlD58VDGypiV94Leflz11woxmgbGY6p_4.png) center / cover"
								}}
							>
								JavaScript Project #1
							</CardTitle>
							<CardText>
								Lorem ipsum dolor sit amet, consectetur
								adipiscing elit.
							</CardText>
							<CardActions border>
								<Button colored>GitHub</Button>
							</CardActions>
						</Card>
					</div>
				);
			case 2: // Case 2 == Java
				return (
					<div className="projects-grid">
						<Card
							shadow={5}
							style={{
								display: "inline-block",
								verticalAlign: "middle",
								width: "300px",
								margin: "1em"
							}}
						>
							<CardTitle
								style={{
									color: "black",
									height: "176px",
									background:
										"url(data:image/jpeg;base64,) center / cover"
								}}
							>
								Java Project #1
							</CardTitle>
							<CardText>
								Lorem ipsum dolor sit amet, consectetur
								adipiscing elit.
							</CardText>
							<CardActions border>
								<Button colored>GitHub</Button>
							</CardActions>
						</Card>
					</div>
				);
			case 3: // Case 3 == OpenGL
				return (
					<div className="projects-grid">
						<Card
							shadow={5}
							style={{
								display: "inline-block",
								verticalAlign: "middle",
								width: "300px",
								margin: "1em"
							}}
						>
							<CardTitle
								style={{
									color: "black",
									height: "176px",
									background:
										"url(data:image/png;base64,) center / cover"
								}}
							>
								OpenGL Project #1
							</CardTitle>
							<CardText>
								Lorem ipsum dolor sit amet, consectetur
								adipiscing elit.
							</CardText>
							<CardActions border>
								<Button colored>GitHub</Button>
							</CardActions>
						</Card>
					</div>
				);
			case 4: // Case 4 == Unity
				return (
					<div className="projects-grid">
						<Card
							shadow={5}
							style={{
								display: "inline-block",
								verticalAlign: "middle",
								width: "300px",
								margin: "1em"
							}}
						>
							<CardTitle
								style={{
									color: "black",
									height: "176px",
									background:
										"url(https://icon-library.net/images/unity-icon-png/unity-icon-png-3.jpg) center / cover"
								}}
							>
								Unity Project #1
							</CardTitle>
							<CardText>
								Lorem ipsum dolor sit amet, consectetur
								adipiscing elit.
							</CardText>
							<CardActions border>
								<Button colored>GitHub</Button>
							</CardActions>
						</Card>
					</div>
				);
			default:
				break;
		}
	};

	render() {
		return (
			<div className="categories">
				<Tabs
					className="categories-tabs"
					activeTab={this.state.activeTab}
					onChange={tabId => this.setState({ activeTab: tabId })}
					ripple
				>
					<Tab style={{ color: "white" }}>React</Tab>
					<Tab style={{ color: "white" }}>JavaScript</Tab>
					<Tab style={{ color: "white" }}>Java</Tab>
					<Tab style={{ color: "white" }}>OpenGL</Tab>
					<Tab style={{ color: "white" }}>Unity</Tab>
				</Tabs>

				<Grid
					col={12}
					style={{
						display: "block",
						width: "100%",
						textAlign: "center"
					}}
				>
					{this.toggleCategories()}
				</Grid>
			</div>
		);
	}
}

export default Projects;
